<template>
  <div>
    <div
      class="flex items-center justify-center m-3 text-black dark:text-white"
    >
      <div class="flex border-2 border-black dark:border-white rounded">
        <input
          type="text"
          class="px-4 py-2 w-5/6 dark:bg-black"
          placeholder="Search..."
          v-model="search"
        />
        <button class="flex items-center justify-center px-4">
          <svg
            class="w-6 h-6 dark:text-white text-black"
            fill="currentColor"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path
              d="M16.32 14.9l5.39 5.4a1 1 0 0 1-1.42 1.4l-5.38-5.38a8 8 0 1 1 1.41-1.41zM10 16a6 6 0 1 0 0-12 6 6 0 0 0 0 12z"
            />
          </svg>
        </button>
      </div>

      <div class="w-1/8">
        <div class="relative ml-4">
          <!-- Dropdown toggle button -->
          <button
            @click="show = !show"
            class="flex items-center justify-between w-full p-2 text-black dark:text-white border-2 border-black dark:border-white rounded-md"
          >
            <span>{{ filter }}</span>
            <svg
              class="w-5 h-5 dark:text-white text-black"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
              fill="currentColor"
            >
              <path
                fill-rule="evenodd"
                d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z"
                clip-rule="evenodd"
              />
            </svg>
          </button>

          <!-- Dropdown menu -->
          <div
            v-show="show"
            class="absolute left-0 py-2 mt-2 w-full bg-white dark:bg-black"
          >
            <button
              @click="
                filter = 'Project Name';
                show = false;
              "
              class="block p-3 text-sm border-b-2 w-full border-black dark:text-white dark:border-white"
            >
              Project Name
            </button>
            <button
              @click="
                filter = 'Artist Name';
                show = false;
              "
              class="block p-3 text-sm border-b-2 w-full border-black dark:text-white dark:border-white"
            >
              Artist Name
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="grid lg:grid-cols-3 xl:grid-cols-3 grid-cols-2 justify-around">
      <div v-for="(item, i) in filteredProjects" :key="i">
        <router-link :to="'/minttoken/' + item.projectId">
          <div
            class="m-3 overflow-hidden shadow-lg border-l-2 border-r-2 border-black dark:border-white p-3"
          >
            <img class="w-full" :src="item.url" alt="img" />
            <div>
              <div
                class="font-bold text-xl m-3 p-3 border-b-2 text-black dark:text-white border-black dark:border-white"
              >
                {{ item.projectName }}
              </div>
              <div
                class="text-gray-700 dark:text-white text-base border-b-2 border-black dark:border-white overflow-hidden m-3 p-3"
              >
                by {{ item.artist }}
              </div>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, onMounted, computed, ref } from "vue";
import { CONTRACT_ABI, CONTRACT_ADDRESS } from "@/assets/artKonnection.js";
import Caver from "caver-js";

export default {
  setup() {
    let projectDetails = reactive([]);

    let filter = ref("Project Name");
    let search = ref("");
    let show = ref(false);
    const filteredProjects = computed(() => {
      return projectDetails.filter((post) => {
        if (filter.value == "Project Name") {
          return post.projectName
            .toLowerCase()
            .includes(search.value.toLowerCase());
        } else if (filter.value == "Artist Name") {
          return post.artist.toLowerCase().includes(search.value.toLowerCase());
        }
      });
    });

    let caver = null;
    let contract = null;

    onMounted(() => {
      caver = new Caver(
        window.klaytn._kaikas.isEnabled()
          ? window.klaytn
          : process.env.VUE_APP_RPC_URL
      );
      contract = new caver.klay.Contract(CONTRACT_ABI, CONTRACT_ADDRESS);
      _nextProjectId();
    });
    const _projectDetails = async (_projectId) => {
      await contract.methods
        .projectDetails(_projectId)
        .call()
        .then((res) => {
          const instance = {
            projectName: res.projectName,
            projectId: _projectId,
            url: `${process.env.VUE_APP_SERVER_URL}/coverassets/${res.projectName}`,
            artist: res.artist,
          };
          projectDetails.push(instance);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const _projectTokenInfo = async (_projectId) => {
      await contract.methods
        .projectTokenInfo(_projectId)
        .call()
        .then(async (res) => {
          if (res.invocations - 1 < res.maxInvocations) {
            _projectDetails(_projectId);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };

    const _nextProjectId = async () => {
      await contract.methods
        .nextProjectId()
        .call()
        .then((result) => {
          for (let i = 0; i < result; i++) {
            _projectTokenInfo(i);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };

    return {
      projectDetails,
      filteredProjects,
      search,
      filter,
      show,
    };
  },
};
</script>
